import React, { useState, useEffect } from "react";
import Container from "@src/components/Container";
import userProvider from "@src/data-access/user-provider";
import { toast } from "react-toastify";
import { Row, Col, Input } from "reactstrap";
import { TextBox } from "@components/InputField/InputField";
import dataSatisfied from "./dataSatisfied.json";
import "./styles.scss";
import { Dropdown } from "react-bootstrap";
import images from "@src/resources/images";

function isPhoneNumber() {
  var re = /^(\+?84|\(\+?84\))[1-9]\d{8,9}$/g;
  return re.test(this);
}
const AboutSatisfied = (props) => {
  const [state, _setState] = useState({
    hospitalName: "Bệnh viện Phổi Trung Ương",
    gender: "1",
    type: 0,
    useCard: "1",
    lisDataKhoa: []
  });
  const data = dataSatisfied.data;
  const setState = (data = {}) => {
    _setState((state) => {
      return { ...state, ...data };
    });
  };

  useEffect(() => {
    const elem = document.getElementById('logo-isofh-fixed')
    if (elem) elem.style.display = 'none';

    userProvider.getAllKhoa().then((s) => {
      return setState({ lisDataKhoa: s.data });
    });
  }, []);

  const onSubmit = (event) => {
    let {
      type,
      gender,
      hospitalName,
      age,
      telephone,
      khoangCach,
      scope,
      useCard,
      a1,
      a2,
      a3,
      a4,
      a5,
      b1,
      b2,
      b3,
      b4,
      b5,
      b6,
      b7,
      b8,
      b9,
      b10,
      c1,
      c2,
      c3,
      c4,
      c5,
      c6,
      c7,
      c8,
      d1,
      d2,
      d3,
      d4,
      e1,
      e2,
      e3,
      e4,
      hospitalAssessment,
      backHospital,
      reason,
      tenPhong,
      tenKhoa
    } = state;
    if (!gender) {
      toast.error("Vui lòng chọn giới tính", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contenthehe");
      elementT.scrollIntoView();
      return;
    } else if (!age) {
      toast.error("Vui lòng nhập tuổi", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contenthehe");
      elementT.scrollIntoView();
      return;
    } else if (!telephone) {
      toast.error("Vui lòng nhập số di động", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contenthehe");
      elementT.scrollIntoView();
      return;
    } else if (!hospitalAssessment) {
      toast.error("Vui lòng đánh giá sự mong đợi", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contentMongdoi");
      elementT.scrollIntoView();
      return;
    } else if (telephone.length < 10) {
      toast.error("Số điện thoại không hợp lệ", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contenthehe");
      elementT.scrollIntoView();
      return;
    } else if (telephone.length > 10) {
      toast.error("Số điện thoại không hợp lệ", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contenthehe");
      elementT.scrollIntoView();
      return;
    } else if (!telephone.isPhoneNumber()) {
      toast.error("Số điện thoại không hợp lệ", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contenthehe");
      elementT.scrollIntoView();
      return;
    } else if (!tenPhong) {
      toast.error("Vui lòng nhập tên phòng", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("content-headinfo");
      elementT.scrollIntoView();
      return;
    } else if (!tenKhoa) {
      toast.error("Vui lòng nhập tên khoa", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("content-headinfo");
      elementT.scrollIntoView();
      return;
    } else if (age.length >= 4) { 
      toast.error("Số tuổi không hợp lệ", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contenthehe");
      elementT.scrollIntoView();
      return;
    }

    if (!khoangCach) {
      toast.error("Vui lòng nhập ước tính khoảng cách!", {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 10000,
      });
      let elementT = document.getElementById("contenthehe");
      elementT.scrollIntoView();
      return;
    }

    if (!backHospital) {
      toast.error(
        "Vui lòng đánh giá Ông/Bà có muốn đánh giá Ông/Bà có quay trở lại hoặc giời thiệu người khác đến hay không",
        {
          position: toast.POSITION.TOP_RIGHT,
          autoClose: 10000,
        }
      );
      return;
    } else if (backHospital === '5' && !reason) {
      toast.error(
        "Vui lòng nhập ý kiến khác của Ông/Bà",
        {
          position: toast.POSITION.TOP_RIGHT,
          autoClose: 10000,
        }
      );
      return;
    } 

    let params = {
      type,
      gender,
      hospitalName,
      age,
      telephone,
      khoangCach: parseInt(khoangCach),
      scope,
      useCard,
      a1,
      a2,
      a3,
      a4,
      a5,
      b1,
      b2,
      b3,
      b4,
      b5,
      b6,
      b7,
      b8,
      b9,
      b10,
      c1,
      c2,
      c3,
      c4,
      c5,
      c6,
      c7,
      c8,
      d1,
      d2,
      d3,
      d4,
      e1,
      e2,
      e3,
      e4,
      hospitalAssessment,
      backHospital,
      reason,
      tenPhong,
      tenKhoa,
      ngayDienPhieu: new Date().format("YYYY-MM-dd")
    };
    userProvider.userSatisfied(params).then((s) => {
      if (s && s.code === 0) {
        toast.success("Thêm ý kiến khảo sát thành công", {
          position: toast.POSITION.TOP_RIGHT,
          autoClose: 10000,
        });
        setTimeout(function () {
          onReset();
        }, 500);
      } else if (s.code === 1000) {
        toast.error("Bạn cần điền đầy đủ thông tin", {
          position: toast.POSITION.TOP_RIGHT,
          autoClose: 10000,
        });
      } else {
        toast.error("Thêm ý kiến khảo sát không thành công", {
          position: toast.POSITION.TOP_RIGHT,
          autoClose: 10000,
        });
      }
    });
  };
  const onReset = () => {
    window.location.reload();
  };
  const onChangeValue = (event) => {
    setState({
      [event.target.name]: event.target.value,
    });
  };
  return (
    <Container
      classApp="about-satisfied"
      title="Phiếu khảo sát ý kiến người bệnh"
    >
       <div>
        <Dropdown className="style-total-dropdow">
          <Dropdown.Toggle
            variant="success"
            id="dropdown-basic"
            className="stylee-dropdow-btn"
          >
            Người bệnh ngoại trú
          </Dropdown.Toggle>
          <Dropdown.Menu className="style-drop-dow-item">
            <Dropdown.Item
              href="/muc-do-hai-long-noi-tru"
              className="style-drop-dow-item"
            >
              Người bệnh nội trú
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </div>
      <div className="container-form">
        <div className="title-form">
          {/* <p>PHIẾU KHẢO SÁT Ý KIẾN NGƯỜI BỆNH NGOẠI TRÚ</p> */}
          <p>
            Bộ câu hỏi dựa theo: Quyết định 3869/QĐ-BYT 2019 về khảo sát hài
            lòng Người bệnh và Nhân viên y tế.
          </p>
          <p className="custome-padd">
            Nhằm mục tiêu nâng cao chất lượng khám, chữa bệnh, đáp ứng sự hài
            lòng người bệnh, Bộ Y tế và bệnh viện tổ chức khảo sát để tìm hiểu
            nguyện vọng người bệnh. Các ý kiến quý báu này sẽ giúp ngành y tế
            khắc phục khó khăn, từng bước cải tiến chất lượng để phục vụ người
            dân tốt hơn. Bộ Y tế bảo đảm giữ bí mật thông tin và không ảnh hưởng
            đến việc điều trị. Xin trân trọng cảm ơn!
          </p>
        </div>
        <div className="info-form">
          <div className="head-info-form" id="content-headinfo">
            <Row>
              <Col lg="6">
                <TextBox
                  styleInput={{ fontWeight: 600 }}
                  label="1. Tên bệnh viện"
                  type="text"
                  placeholder="………………………………………………………………."
                  value={state.hospitalName}
                  readOnly
                />
              </Col>
              <Col lg="6">
                <TextBox
                  styleInput={{ fontWeight: 600 }}
                  label="2. Ngày điền phiếu"
                  type="text"
                  placeholder="………………………………………………………………."
                  value={new Date().format("dd/MM/YYYY")}
                  readOnly
                />
              </Col>
              <Col lg="6">
                <label className="nguoitraloi-style">
                  3. Khoa điều trị ngoại trú
                  <strong style={{ color: "red" }}> *</strong>
                </label>
                <select className="custom-select"
                  value={state.tenKhoa}
                  name="tenKhoa"
                  onChange={(e) => onChangeValue(e)}
                  placeholder="Chọn khoa"
                >
                  <option key='default' disabled selected value
                    style={{ display: 'none' }}
                  >Chọn khoa...</option>
                  {(state.lisDataKhoa || []).map((item) => {
                    return <option style={{ fontSize: '1em' }} key={item.name} value={item.name} >{item.name}</option>;
                  })}
                </select>
              </Col>
              <Col lg="6">
                <label className="nguoitraloi-style">
                  4. Phòng
                  <strong style={{ color: "red" }}> *</strong>
                </label>
                <TextBox
                    name="tenPhong"
                    placeholder="Nhập phòng"
                    value={state.tenPhong}
                    onChangeValue={onChangeValue}
                  />
              </Col>
            </Row>
          </div>
          <div className="form-info" id="contenthehe">
            <p className="title-info-form">THÔNG TIN NGƯỜI BỆNH</p>
            <div className="user-info-inner">
              <Row>
                <Col lg="6">
                  <label>A1. Giới tính <strong style={{ color: "red" }}>*</strong></label>
                  <div className="user-info-radio">
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="gender"
                        value="1"
                        checked={state.gender === "1"}
                        onClick={(e) => onChangeValue(e)}
                      />
                      <span className="checkmark">Nam</span>
                    </label>
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="gender"
                        value="2"
                        onClick={(e) => onChangeValue(e)}
                      />
                      <span className="checkmark"> Nữ</span>
                    </label>
                  </div>
                </Col>
                <Col lg="6">
                  <label>A2. Tuổi <strong style={{ color: "red" }}>*</strong></label>
                  <TextBox
                    type="number"
                    name="age"
                    placeholder="Nhập tuổi"
                    value={state.age}
                    maxLength="2"
                    onChangeValue={(e) => {
                      if (e.target.value.length < 4) {
                        onChangeValue(e);
                      }
                    }}
                  />
                </Col>
              </Row>
              <Row>
                <Col lg="6">
                  <label>A3. Số di động <strong style={{ color: "red" }}>*</strong></label>
                  <TextBox
                    type="number"
                    name="telephone"
                    placeholder="Nhập số di động"
                    value={state.telephone}
                    onChangeValue={(e) => {
                      onChangeValue(e);
                    }}
                  />
                </Col>
                <Col lg="6">
                  <label>
                    A4. Ước tính khoảng cách từ nơi bạn sống tới bệnh viện
                    (km) <strong style={{ color: "red" }}>*</strong>
                  </label>
                  <div className="style-total-nam-vien">
                    <input
                      onChange={(e) => {
                        if (e.target.value.length < 4) {
                          onChangeValue(e);
                        }
                      }}
                      placeholder="Nhập khoảng cách"
                      type="number"
                      name="khoangCach"
                      value={state.khoangCach}
                    ></input>
                  </div>
                </Col>
              </Row>
              <Row>
                <Col lg="6">
                  <label>
                    A5. Ông/Bà có sử dụng thẻ BHYT cho lần điều trị này không?{" "}
                    <strong style={{ color: "red" }}>*</strong>
                  </label>
                  <div className="user-info-radio">
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="useCard"
                        value="1"
                        checked={state.useCard === "1"}
                        onClick={(event) => {
                          onChangeValue(event);
                        }}
                      />
                      <span className="checkmark">Có</span>
                    </label>
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="useCard"
                        value="0"
                        onClick={(event) => {
                          onChangeValue(event);
                        }}
                      />
                      <span className="checkmark">Không</span>
                    </label>
                  </div>
                </Col>
              </Row>
            </div>
          </div>
        </div>
        <div className="form-info">
          <p className="title-info-form">ĐÁNH GIÁ VIỆC SỬ DỤNG DỊCH VỤ Y TẾ</p>
          <div className="user-info-inner">
            <p>
              <b>
                Ông/Bà đánh dấu gạch chéo vào một số từ 1 đến 5, tương ứng với
                mức độ hài lòng hoặc nhận xét từ rất kém đến rất tốt cho từng
                câu hỏi dưới đây:
              </b>
            </p>
            <ul className="list-evaluate">
              <li>
                <span>1</span> <br />
                Rất không hài lòng <br /> <i>hoặc:</i> Rất kém
              </li>
              <li>
                <span>2</span> <br />
                Không hài lòng <br /> <i>hoặc:</i> Kém
              </li>
              <li>
                <span>3</span> <br />
                Bình thường <br /> <i>hoặc:</i> Trung bình
              </li>
              <li>
                <span>4</span> <br />
                Hài lòng <br /> <i>hoặc:</i> Tốt
              </li>
              <li>
                <span>5</span> <br />
                Rất hài lòng <br /> <i>hoặc:</i> Rất tốt
              </li>
            </ul>
          </div>
        </div>
        <div className="evaluate">
          <table className="tbl-eval">
            {data &&
              data.length &&
              data.map((item, i) => {
                return (
                  <tbody key={i}>
                    <tr>
                      <td colSpan="4" className="title-table-eval">
                        {item.title}
                      </td>
                    </tr>
                    {item.data && item.data.length
                      ? item.data.map((item1, index1) => {
                          return (
                            <tr key={index1}>
                              <td style={{ width: "7%" }}>{item1.code}</td>
                              <td style={{ width: "68%" }} colSpan="2">
                                {item1.content}
                              </td>
                              <td style={{ width: "25%" }}>
                                <div className="display-radio">
                                  <label className="containerCheck">
                                    <input
                                      type="radio"
                                      name={item1.name}
                                      value="1"
                                      onClick={(e) => onChangeValue(e)}
                                    />
                                    <span className="checkmark">1</span>
                                  </label>
                                  <label className="containerCheck">
                                    <input
                                      type="radio"
                                      name={item1.name}
                                      value="2"
                                      onClick={(e) => onChangeValue(e)}
                                    />
                                    <span className="checkmark">2</span>
                                  </label>
                                  <label className="containerCheck">
                                    <input
                                      type="radio"
                                      name={item1.name}
                                      value="3"
                                      onClick={(e) => onChangeValue(e)}
                                    />
                                    <span className="checkmark">3</span>
                                  </label>
                                  <label className="containerCheck">
                                    <input
                                      type="radio"
                                      name={item1.name}
                                      value="4"
                                      onClick={(e) => onChangeValue(e)}
                                    />
                                    <span className="checkmark">4</span>
                                  </label>
                                  <label className="containerCheck">
                                    <input
                                      type="radio"
                                      name={item1.name}
                                      value="5"
                                      onClick={(e) => onChangeValue(e)}
                                    />
                                    <span className="checkmark">5</span>
                                  </label>
                                </div>
                              </td>
                            </tr>
                          );
                        })
                      : ""}
                  </tbody>
                );
              })}
            <tbody>
              <tr>
                <td>F</td>
                <td colSpan="2" id="contentMongdoi">
                  Đánh giá chung bệnh viện đã đáp ứng được bao nhiêu % so với
                  mong đợi trước khi tới khám bệnh?
                  <span style={{ color: "red" }}>*</span>
                  <br />
                  <span style={{ fontWeight: 400, color: "#999999" }}>
                    <i>
                      (điền số từ 0% đến 100% hoặc có thể điền trên 100% nếu
                      bệnh viện điều trị tốt, vượt quá mong đợi của Ông/Bà)
                    </i>
                  </span>
                </td>
                <td className="hospital-assessment">
                  <TextBox
                    name="hospitalAssessment"
                    type="number"
                    placeholder="Nhập số %"
                    onChangeValue={(e) => {
                      onChangeValue(e);
                    }}
                  />
                  <span>%</span>
                </td>
              </tr>
              <tr>
                <td>G</td>
                <td style={{ width: "45%" }}>
                  <div>
                    Nếu có nhu cầu khám bệnh, Ông/Bà có quay trở lại hoặc giới
                    thiệu cho người khác đến không?
                  </div>
                </td>
                <td colSpan="2">
                  <p className="display-radio-last">
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="backHospital"
                        value="1"
                        onClick={(e) => {
                          setState({
                            isShowReason: false,
                            reason: "",
                          });
                          onChangeValue(e);
                        }}
                      />
                      <span className="checkmark">
                        1. Chắc chắn không bao giờ quay lại
                      </span>
                    </label>
                  </p>
                  <p className="display-radio-last">
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="backHospital"
                        value="2"
                        onClick={(e) => {
                          setState({
                            isShowReason: false,
                            reason: "",
                          });
                          onChangeValue(e);
                        }}
                      />
                      <span className="checkmark">
                        2. Không muốn quay lại nhưng có ít lựa chọn khác
                      </span>
                    </label>
                  </p>
                  <p className="display-radio-last">
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="backHospital"
                        value="3"
                        onClick={(e) => {
                          setState({
                            isShowReason: false,
                            reason: "",
                          });
                          onChangeValue(e);
                        }}
                      />
                      <span className="checkmark">3. Có thể sẽ quay lại</span>
                    </label>
                  </p>
                  <p className="display-radio-last">
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="backHospital"
                        value="4"
                        onClick={(e) => {
                          setState({
                            isShowReason: false,
                            reason: "",
                          });
                          onChangeValue(e);
                        }}
                      />
                      <span className="checkmark">
                        4. Chắc chắn sẽ quay lại hoặc giới thiệu cho người khác
                      </span>
                    </label>
                  </p>
                  <p className="display-radio-last">
                    <label className="containerCheck">
                      <input
                        type="radio"
                        name="backHospital"
                        value="5"
                        onClick={(e) => {
                          setState({
                            isShowReason: true,
                          });
                          onChangeValue(e);
                        }}
                      />
                      <span className="checkmark">5. Khác (ghi rõ)</span>
                      &nbsp;
                    </label>
                  </p>
                </td>
              </tr>
              {state.isShowReason ? (
                <tr>
                  <td>H</td>
                  <td style={{ width: "45%" }}>
                    <div>
                        Ông/Bà có ý kiến gì khác, xin ghi rõ?{" "}
                        <strong style={{ color: "red" }}>*</strong>
                    </div>
                  </td>
                  <td colSpan="2">
                    <textarea
                      type="text"
                      value={state.h}
                      className="styine-area"
                      placeholder="Nhập ý kiến của Ông/Bà"
                      name="reason"
                      onChange={(e) => {
                        onChangeValue(e);
                      }}
                    />
                  </td>
                </tr>
              ) : (
                ""
              )}
            </tbody>
          </table>
        </div>
        <div className="submit-form">
          <button onClick={() => onSubmit()}>
            <div>
              <span>GỬI</span>
              <img src={images.ic_send} alt=""/>
            </div>
          </button>
          <p className="noteForm">XIN TRÂN TRỌNG CẢM ƠN ÔNG/BÀ!</p>
          <img className="logoFooter" src={images.isofh_logo_footer} alt=""/>
        </div>
      </div>
    </Container>
  );
};

export default AboutSatisfied;
